@import "../../variables.scss";

.Icon {
  float: right;
  margin: auto 0.2rem;
  vertical-align: middle;
  cursor: pointer;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 3rem;
  height: 3rem;
  padding: 0;
  border-radius: 50%;

  overflow: hidden;
  position: relative;

  & > *::selection {
    background: none;
  }
}

.Icon .ripple2 {
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.7);

  position: absolute;
  transform: scale(0);
  animation: ripple 0.6s linear;
}

@keyframes ripple {
  to {
    transform: scale(2.5);
    opacity: 0;
  }
}
